@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700,700i&display=swap);

section.react-box{
    padding: 30px 10px;
    background: #e5e5e5;
    color: #222;
    display: flex;
    justify-content: space-evenly;
    align-items: stretch;
    flex-wrap: wrap;
}
section.react-box.column{
    flex-direction: column;
    align-items: center;
}
section.react-box h2.react-box-title{
    text-transform: uppercase;
    margin: 0 0 20px 0;
    font-size: 2em;
    width: 100%;
    overflow-wrap: break-word;
    font-weight: bold;
    text-align: center;
}
a.react-card{
    color: #222;
    min-width: 20vw;
    max-width: 250px;
    margin: 15px 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;
    border-radius: 6px;
    box-shadow: 3px 3px 10px #2600ff96;
    cursor: pointer;
    text-align: center;
}
a.react-card:hover{
    text-shadow: none;
}
a.react-card .card-header{
    width: 100%;
    height: 100%;
    color: #fff;
    background: #451d82;
    background: -webkit-linear-gradient(45deg, #a400ff, #2700ff);
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 28px;
}
a.react-card:hover .card-header{
    background: #e5d81d;
    background: -webkit-linear-gradient(45deg, #8b8532, #ffee00, #b6ac20);
}
a.react-card:hover{
    border: none;
}
a.react-card .card-body{
    padding: 10px;
    width: 100%;
    height: 200px;
    font-size: 22px;
    color: #2600ffcc;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
a.react-card .card-body.simple{
    font-size: 30px;
    font-weight: bold;
}
a.react-card .card-footer{
    padding: 10px;
    width: 100%;
    height: 90px;
    color: #888;
    font-size: 15px;
}
a.react-card img{
    width: 40%;
}
a.react-card.dark{
    color: #e5e5e5;
}


.highlight{
    color: #ffc200;
}
img#dev-pic{
    border-radius: 6px;
    box-shadow: 5px 5px 15px #22222288;
    margin: auto auto 20px auto;
}

p#bio{
    text-align: center;
    font-size: 18px;
}
*{
  --white: #e5e5e5;
  --blue: #2700ff;
  --fuchsia: #a400ff;
  --dark: #0c0b0d;
  --yellow: #ffc200;
}
header#header{
    width: 100%;
    height: 60vh;
    color: #fff;
    font-family: monospace;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(/static/media/theme.f1b0fe4f.png);
    background-attachment: fixed;
    background-size: cover;
}
header#header #header-background{
    background: #000;
    width: 100%;
    height: 100%;
    -webkit-filter: opacity(.9);
            filter: opacity(.9);
}
header#header #header-box{
    padding: 10px;
    border-top: 1px solid var(--white);
    border-bottom: 1px solid var(--white);
    max-width: 50vw;
    position: absolute;
    z-index: 1;
    text-shadow: 4px 4px 15px var(--blue);
    -webkit-transition: text-shadow .05s ease-in-out;
    transition: text-shadow .05s ease-in-out;
}
header#header #header-box:hover{
    text-shadow: 4px 4px 15px var(--fuchsia);
}

header#header #header-title{
    font-size: 60px;
    text-transform: capitalize;
}
header#header #header-subtitle{
    font-size: 2em;
    text-transform: capitalize;
    color: #ccc;
}

@media only screen and (max-width:720px){
    header#header #header-box{
        max-width: 90vw;
    }
    header#header #header-title{
        font-size: 42px;
    }
    header#header #header-subtitle{
        font-size: 1.5em;
    }
}

nav#navbar{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    background: var(--blue);
    background: linear-gradient(45deg, var(--blue), var(--fuchsia));
    padding: 20px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

nav#navbar ul#navbar-links{
    list-style: none;
    font-size: 22px;
}
nav#navbar ul#navbar-links li{
    display: inline;
    margin: 2px 5px;
}
nav#navbar #navbar-logo{
    font-weight: bold;
    color: var(--white);
    font-size: 30px;
    text-shadow: 2px 3px 30px var(--fuchsia);
}
nav#navbar #navbar-logo span.highlight{
    color: var(--yellow);
}

@media only screen and (max-width: 720px){
    nav#navbar #navbar-logo{
        font-size: 25px;
    }
    nav#navbar ul#navbar-links{
        font-size: 18px;
    }
}

a{
    color: var(--white);
    text-decoration: none;
    border-bottom: 1px solid #a400ff00;
    -webkit-transition: all .1s ease-in-out;
    transition: all .1s ease-in-out;
}
a:hover{
    border-bottom: 1px solid var(--yellow);
    text-shadow: 2px 3px 10px var(--yellow);
}
footer#footer{
    padding: 30px;
    background: #541d82;
    color: #e5e5e5;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 18px;
    font-weight: bold;
}
footer#footer .highlight{
    color: #ffc200;
}
footer#footer ul#footer-links{
    margin-top: 10px;
    list-style: none;
}
footer#footer ul#footer-links li{
    display: inline;
    margin: 2px 5px;
    font-size: 30px;
    -webkit-transition: all .1s ease-in-out;
    transition: all .1s ease-in-out;
}

footer#footer ul#footer-links li a:hover{
    font-size: 32px;
    color: #fbff00;
}   

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --white: #e5e5e5;
  --blue: #2700ff;
  --fuchsia: #a400ff;
  --dark: #0c0b0d;
  --yellow: #ffc200;
}

html, body, #root{
    min-height: 100%;
    background: var(--white);
    color: var(--dark);
    font-family: monospace, 'Roboto', sans-serif;
}

html{
    scroll-behavior: smooth;
}
